.Details,
details {
  margin-bottom: 1.5rem;
  /* transition: all ease-out .5s; */
  /* padding: 1rem; */

  color: var(--bravo);
  background-color: var(--echo);

  overflow: hidden;
}

.Details__summary,
summary {
  position: relative;
  display: block;

  width: 100%;

  padding: 1rem;

  &:hover {
    cursor: pointer;
  }

  &:focus-visible {
    outline: 2px solid var(--bravo);
    /* outline-offset: calc(1rem + 3px); */
    outline-offset: -2px;
  }
}

.Details__summary-text::after {
  position: absolute;
  top: 0;
  right: 0;

  display: block;

  height: 100%;
  content: "";
  content: ""/ "";
  transition: transform 0.3s;
  transform: rotate(90deg);
  transform-origin: 50% 50%;

  color: var(--foreground);
  background-image: url("data:image/svg+xml;base64,");
  background-repeat: no-repeat;
  background-size: 99%;
  aspect-ratio: 1/1;

  speak: none;

  @media reader, speech, aural {
    display: none;
    visibility: hidden;
  }
}

.Details__summary-text {
  position: relative;
  display: block;

  width: 100%;

  color: var(--alpha);

  font-size: 1rem;
}

.Details[open] .Details__summary {
  background-color: var(--delta);
}

.Details[open] .Details__summary-text::after {
  transition: transform 0.3s;
  /* content: '\2297'; */
  transform: rotate(45deg);
}

.Details__content {
  padding: 2rem 1rem;
  transition: height 0.2s;

  color: var(--alpha);

  font-size: 1rem;
}

.Details__content *:last-child {
  margin-bottom: 0;
}

details > summary::details-marker {
  display: none;
}

details > summary::marker {
  display: none;
}

summary::-webkit-details-marker {
  display: none;
}

details > summary {
  list-style-type: none;
}

body.no-js {
  & .Details {
    display: block !important;
  }

  & .Details__content {
    display: block !important;
  }
}

